import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { showNotification } from '../Utils'
import config from '../../config/configO2023.json'

const CreneauPropositionPopup = ({
  expertOrBoss,
  tpiData,
  schedule,
  fermerPopup
}) => {
  const [propositions, setPropositions] = useState([])
  const [selectedDate, setSelectedDate] = useState('')
  // const [selectedDate, setSelectedDate] = useState('');
  const [selectedCreneau, setSelectedCreneau] = useState('')
  const [forceRender, setForceRender] = useState(false)

  const { year } = useParams() // Extrait l'anne des paramtres d'URL

  // Pour accder  la variable d'environnement REACT_APP_DEBUG
  const debugMode = process.env.REACT_APP_DEBUG === 'true'; // Convertir en boolen si ncessaire

  // Pour accder  la variable d'environnement REACT_APP_API_URL
  const apiUrl = process.env.REACT_APP_API_URL;


  useEffect(() => {
    if (schedule.length > 0) {
      const premierCreneau = `${schedule[0].startTime} - ${schedule[0].endTime}`
      setSelectedCreneau(premierCreneau)
    }
  }, [schedule])

  const ajouterProposition = () => {
    // Vrification de la slection de la date et du crneau
    if (selectedDate && selectedCreneau) {
      if (propositions.length < 3) {
        // Vrifie les doublons avant d'ajouter
        if (
          !propositions.some(
            prop =>
              prop.date === selectedDate && prop.creneau === selectedCreneau
          )
        ) {
          setPropositions([
            ...propositions,
            { date: selectedDate, creneau: selectedCreneau }
          ])
        } else {
          showNotification('Cette proposition a dj t faite.', 2000)
        }
      } else {
        showNotification('Vous ne pouvez pas ajouter plus de 3 propositions.')
      }
    } else {
      showNotification('Veuillez slectionner une date et un crneau.')
    }
  }

  const handleDateChange = e => {
    setSelectedDate(e.target.value)
  }

  const sauvegarderPropositions = async () => {
    const url = `${apiUrl}/api/save-propositions/${year}/${expertOrBoss}/${tpiData.id}`

    const propositionsData = {
      isValidated: false,
      submit: propositions
    }

    try {
      const response = await envoyerRequete(url, 'PUT', propositionsData)
      traiterReponse(response)
    } catch (error) {
      console.error('Erreur :', error)
      afficherNotificationErreur()
    } finally {
      // Mettre  jour l'tat pour forcer un re-render
      setForceRender(prevState => !prevState)
      fermerPopup()
    }
  }

  const envoyerRequete = async (url, method, body) => {
    const response = await fetch(url, {
      method: method,
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(body)
    })

    if (!response.ok) {
      throw new Error(
        'Erreur lors de la mise  jour des propositions acceptes'
      )
    }
    return response
  }

  const traiterReponse = response => {
    showNotification('Propositions mises  jour avec succs', 'success')
  }

  const afficherNotificationErreur = () => {
    showNotification('Erreur lors de la mise  jour de la proposition', 'error')
  }

  // Conversion des crneaux en  pair de priodes
  const p = ['01', '03', '05', '07', '09', '11', '13', '15', '17']

  return (
    <div className='popup-container'>
      <div className='popup'>
        <h3>Propositions de crneau pour {tpiData?.candidat}</h3>

        <select value={selectedDate} onChange={handleDateChange}>
          <option value=''>Slectionnez une date</option>
          {config.soutenanceDates.map(date => (
            <option key={date} value={date}>
              {date}
            </option>
          ))}
        </select>

        <select
          value={selectedCreneau}
          onChange={e => setSelectedCreneau(e.target.value)}
        >
          {schedule.map((creneau, index) => (
            <option
              key={`select_${index}-${creneau}`}
              value={`${creneau.startTime} - ${creneau.endTime}`}
            >
              {`P${p[index]} \u00A0\u00A0 ${creneau.startTime} - ${creneau.endTime}`}
            </option>
          ))}
        </select>

        {/* Dsactive le bouton si la date ou le crneau n'est pas slectionn */}
        <button
          id='btnAddDate'
          onClick={ajouterProposition}
          disabled={!selectedDate || !selectedCreneau}
        >
          Ajouter
        </button>
        <ul>
          <h4> Ci-dessous vos propositions : </h4>
          {propositions.map((prop, index) => (
            <li key={`Creneau_${index}`}>{`${prop.date} - ${prop.creneau}`}</li>
          ))}
        </ul>
        <span>
          <button id='btnSave' onClick={sauvegarderPropositions}>
            Sauvegarder
          </button>

          <button id='btnClose' onClick={fermerPopup}>
            Fermer
          </button>
        </span>
      </div>
    </div>
  )
}
export default CreneauPropositionPopup
