import axios from "axios";

// Pour accder  la variable d'environnement REACT_APP_DEBUG
const debugMode = process.env.REACT_APP_DEBUG === 'true'; 

// Pour accder  la variable d'environnement REACT_APP_API_URL
const apiUrl = debugMode ? process.env.REACT_APP_API_URL_TRUE : process.env.REACT_APP_API_URL_FALSE

export const createTpiModel = async (modelData) => {
  try {
    console.log(`${apiUrl}/save-tpi`, modelData)
    const response = await axios.post(`${apiUrl}/api/save-tpi`, modelData);
    return response.data;
  } catch (error) {
    console.error("Erreur lors de la cration du modle de TPI:", error);
    throw new Error(
      "Une erreur est survenue lors de la cration du modle de TPI. Veuillez ressayer plus tard."
    );
  }
};

export const getTpiModels = async () => {
  try {
    console.log(`${apiUrl}/api/get-tpi`)
    const response = await axios.get(`${apiUrl}/api/get-tpi`);
    return response.data;
    
  } catch (error) {
    console.error("Erreur lors de la rcupration des modles de TPI:", error);
    throw new Error("Une erreur est survenue lors de la rcupration des modles de TPI. Veuillez ressayer plus tard.");
  }
};

export const updateTpiModel = async (modelId, updateData) => {
  try {
    const response = await axios.put(
      `${apiUrl}/update-tpi/${modelId}`,
      updateData
    );
    return response.data;
  } catch (error) {
    console.error("Erreur lors de la mise  jour du modle de TPI:", error);
    throw new Error(
      "Une erreur est survenue lors de la mise  jour du modle de TPI. Veuillez ressayer plus tard."
    );
  }
};

export const deleteTpiModel = async (modelId) => {
  try {
    const response = await axios.delete(`${apiUrl}/delete-tpi/${modelId}`);
    return response.data;
  } catch (error) {
    console.error("Erreur lors de la suppression du modle de TPI:", error);
    throw new Error(
      "Une erreur est survenue lors de la suppression du modle de TPI. Veuillez ressayer plus tard."
    );
  }
};
