import React, { useState, useEffect } from 'react';
import axios from 'axios';
import CryptoJS from 'crypto-js';

// Pour accder  la variable d'environnement REACT_APP_DEBUG
const debugMode = process.env.REACT_APP_DEBUG === 'true'; // Convertir en boolen si ncessaire
// Pour accder  la variable d'environnement REACT_APP_API_URL
const apiUrl = debugMode ? process.env.REACT_APP_API_URL_TRUE : process.env.REACT_APP_API_URL_FALSE;

console.log('val de apiurl : ',apiUrl)

const TokenGenerator = () => {
  const [secretKey, setSecretKey] = useState('');
  const [experts, setExperts] = useState([]); // Renomm pour reflter qu'il contient plus que des emails
  const [generatedUrls, setGeneratedUrls] = useState([]);
  const [selectedYear, setSelectedYear] = useState(2024); // Ajout de l'tat pour l'anne slectionne

  useEffect(() => {
    const loadEmails = async () => {
      try {
        const response = await axios.get(`${apiUrl}/api/experts/emails`);
        setExperts(response.data); // Ajust pour utiliser setExperts
      } catch (error) {
        console.error('Erreur lors du chargement des donnes:', error);
      }
    };

    loadEmails();
  }, []);

  const handleGenerateTokens = async () => {
    console.log('Dbut de la gnration des tokens...');
    const urls = [];
    const update = [];

    console.log(experts)

    for (const { email, name } of experts) { // Dstructuration pour obtenir email et name
      const token = CryptoJS.SHA256(email + secretKey).toString();
      const url = `/calendrierDefenses/${selectedYear}?token=${token}`; // Utilisation de l'anne slectionne

      update.push({ token, email, name });
      urls.push({ email, name, url });
    }
    try {
      const response = await axios.put(`${apiUrl}/api/experts/putTokens`, update);

    } catch (error) {
      console.error(`Erreur lors de la sauvegarde dans la base de donnes pour ${update} :`, error);
    }

    console.log('Gnration des tokens termine.');
    setGeneratedUrls(urls);
  };

  return (
    <div className="container" style={{ marginTop: "30px" }}>
      <h2>Gnrateur de Tokens</h2>
      <div className="form-group">
        <label htmlFor="year">Anne :</label>
        <select id="year" className="form-control" value={selectedYear} onChange={e => setSelectedYear(parseInt(e.target.value))}>
          {[2022, 2023, 2024, 2025, 2026, 2027].map(year => (
            <option key={year} value={year}>{year}</option>
          ))}
        </select>
      </div>
      <div className="form-group">
        <input
          type="text"
          className="form-control"
          placeholder="Cl secrte"
          value={secretKey}
          onChange={e => setSecretKey(e.target.value)}
        />
      </div>
      <div className="form-group">
        <button className="btn btn-primary" onClick={handleGenerateTokens}>Gnrer Tokens</button>
      </div>
      <ul className="list-group">
        {generatedUrls.map(({ email, name, url }, index) => (
          <li key={index} className="list-group-item">
            <strong>{name} ({email}):
              <br />
            </strong> <a href={url} target="_blank" rel="noopener noreferrer">{url}</a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default TokenGenerator;
