import { createTpiModel, getTpiModels } from "../tpiControllers/TpiController";
import { showNotification } from "../Utils";

// Fonction pour sauvegarder un TPI dans la base de donnes via le serveur
export const saveTpiToServer = async (tpiDetails) => {
  console.log("Save TPI to server: ", tpiDetails);

  // Vrifier si les champs obligatoires sont remplis
  if (!tpiDetails.refTpi || !tpiDetails.candidat || !tpiDetails.boss) {
    // Afficher un message d'erreur indiquant que certains champs sont obligatoires
    showNotification("Veuillez remplir tous les champs obligatoires.",3000);
    // Sortir de la fonction sans sauvegarder les donnes s'il manque des champs obligatoires
    return;
  }
  try {
    // Appeler la fonction du client pour crer ou mettre  jour le modle de TPI dans la base de donnes via le serveur
    const savedModel = await createTpiModel(tpiDetails);

    // Afficher le message en consquence
    const message = savedModel
      ? "TPI sauvegard avec succs - ref " + savedModel.refTpi
      : "Erreur lors de la sauvegarde du TPI";

      showNotification(message,3000);
      
  } catch (error) {
    console.error("Erreur lors de la sauvegarde du TPI:", error);
    // Afficher un message d'erreur en cas d'chec de la sauvegarde
    const errorMessage = document.createElement("div");
    errorMessage.innerText =
      "Erreur lors de la sauvegarde du TPI. Veuillez ressayer plus tard.";
    errorMessage.className = "errorMessage show";
    document.body.appendChild(errorMessage);

    // Supprimer le message d'erreur aprs quelques secondes (par exemple, 3 secondes)
    setTimeout(() => {
      errorMessage.classList.remove("show");
      document.body.removeChild(errorMessage);
    }, 3000);
  }
};

// Ancienne fonction pour sauvegarder un TPI dans localStorage (version locale)
export const saveTpi = (tpiDetails) => {
  console.log("Save TPI: ", tpiDetails);

  // Vrifier si les champs obligatoires sont remplis
  if (!tpiDetails.refTpi || !tpiDetails.candidat || !tpiDetails.boss) {

    // Afficher un message d'erreur indiquant que certains champs sont obligatoires
    showNotification("Veuillez remplir tous les champs obligatoires.", 3000);
    // Sortir de la fonction sans sauvegarder les donnes s'il manque des champs obligatoires
    return;
  }

  // Rcuprer la liste des TPIs existants depuis le localStorage
  const tpiList = JSON.parse(localStorage.getItem("tpiList")) || [];

  // Vrifier si le TPI existe dj en fonction de son refTpi
  const existingTpiIndex = tpiList.findIndex(
    (tpi) => tpi.refTpi === tpiDetails.refTpi
  );

  let isUpdate = false; // Variable pour indiquer si c'est une mise  jour

  if (existingTpiIndex !== -1) {
    // Si le TPI existe dj (mise  jour), remplacez les donnes du TPI existant
    tpiList[existingTpiIndex] = tpiDetails;
    isUpdate = true; // Marquer comme mise  jour
  } else {
    // Si le TPI n'existe pas (nouvelle sauvegarde), ajoutez-le  la liste
    tpiList.push(tpiDetails);
  }

  // Enregistrez la liste mise  jour des TPIs dans le localStorage
  localStorage.setItem("tpiList", JSON.stringify(tpiList));

  // Afficher le message en consquence
  const message = isUpdate
    ? "Mise  jour du TPI ref " + tpiDetails.refTpi
    : "Sauvegarde du TPI ref " + tpiDetails.refTpi;

  showNotification(message, 3000);
};

// Fonction pour rcuprer la liste des TPI depuis localStorage
export const getTpiList = () => {
  return JSON.parse(localStorage.getItem("tpiList")) || [];
};

// Fonction pour rcuprer la liste des TPI depuis le serveur
export const getTpiFromServer = async () => {
  try {
    // Appeler la fonction du client pour rcuprer les modles de TPI depuis le serveur
    const tpiModels = await getTpiModels();

    // Afficher le message en cas de succs
    console.log("TPI rcuprs depuis le serveur:", tpiModels);

    return tpiModels;
  } catch (error) {
    console.error(
      "Erreur lors de la rcupration des TPI depuis le serveur:",
      error
    );

    showNotification(
      "Erreur lors de la rcupration des TPI depuis le serveur. Veuillez ressayer plus tard.",
      3000
    );
  }
};
