import axios from 'axios'

// Pour accder  la variable d'environnement REACT_APP_DEBUG
const debugMode = process.env.REACT_APP_DEBUG === 'true'; // Convertir en boolen si ncessaire
// Pour accder  la variable d'environnement REACT_APP_API_URL
const apiUrl = process.env.REACT_APP_API_URL;

const saveTpiRoomUrl = `${apiUrl}/api/save-tpi-rooms`
const getTpiRoomsUrl = `${apiUrl}/api/get-tpi-rooms`

// Fonction pour transmettre les donnes  la base de donnes
export const transmitToDatabase = async (data) => {
  // Obtenir l'anne courante

  const currentYear = data.date.substring(0,4); 
  const url = `${saveTpiRoomUrl}/${currentYear}`;

  try {
    const response = await axios.post(url, data);

    if (response.status === 200) {
      return true
    } else {
      return false
    }
  } catch (error) {
    console.error('Erreur lors de la transmission des donnes  la base de donnes :', error);
  }
}

export const checkRoomExistenceById = async idRoom => {
  // Validation de l'ID de la salle
  if (!idRoom) {
    throw new Error("L'ID de la salle est requis pour la vrification.");
  }

  try {
    const response = await axios.get(`${apiUrl}/api/check-room-existence/${idRoom}`);

    console.log("Rponse reue de l'API pour l'existence de la salle:", response.data);

    // Vrifier si la rponse est valide et contient les champs ncessaires
    if (response.data && 'exists' in response.data && '_id' in response.data) {
      return response.data; // { exists: true/false, _id: roomId }
    } else {
      throw new Error("Rponse de l'API invalide ou incomplte.");
    }
  } catch (error) {
    console.error(`Erreur lors de la vrification de l'existence de la salle: ${error.message}`);
    throw error; // Propager l'erreur pour une gestion externe
  }
}

export const createTpiRooms = async roomData => {
  try {
    const existingRoom = await checkRoomExistenceById(roomData.idRoom);
    if (existingRoom && existingRoom.exists) {
      // Si la salle existe dj, la mettre  jour
      const updatedRoom = await updateTpiRoom(existingRoom._id, roomData);
      return updatedRoom;
    } else {
      // Si la salle n'existe pas, en crer une nouvelle
      const response = await axios.post(saveTpiRoomUrl, roomData);
      return response.data;
    }
  } catch (error) {
    console.error(`Erreur lors de la cration ou de la mise  jour de la salle de TPI: ${error.message}`);
    throw error;
  }
};


export const createTpiCollectionForYear = async (year, roomData) => {
  try {
    const response = await axios.post(`${apiUrl}/create-tpi-collection/${year}`, roomData);
    if (response.status === 200) {
      console.log(`Collection TPI pour l'anne ${year} cre avec succs.`);
      return response.data;
    } else {
      console.error(`Erreur lors de la cration de la collection TPI pour l'anne ${year}`);
      return null;
    }
  } catch (error) {
    console.error(`Erreur rseau lors de la cration de la collection TPI pour l'anne ${year}: ${error.message}`);
    throw error;
  }
};

export const getTpiRooms = async () => {
  try {
    const response = await axios.get(getTpiRoomsUrl)
    return response.data
  } catch (error) {
    console.error(
      `Erreur lors de la rcupration des salles de TPI: ${error.message}`
    )
    throw error
  }
}

// Fonction pour mettre  jour une salle de TPI
export const updateTpiRoom = async (roomId, roomData) => {
  // Validation de base des entres
  if (!roomId || !roomData) {
    throw new Error("L'ID de la salle et les donnes sont requis pour la mise  jour.");
  }

  try {
    // Utilisez l'URL complte pour l'endpoint de mise  jour
    console.log("updateTpiRoom: ", roomId, " ici data: ", roomData)

    const response = await axios.put(`${apiUrl}/update-tpi-room/${roomId}`, roomData);

    // Vrification de la rponse de l'API
    if (!response || response.status !== 200) {
      throw new Error('La rponse de la mise  jour de la salle de TPI est invalide.');
    }

    // Renvoyez les donnes mises  jour ou un message de succs
    return response.data || { message: 'Mise  jour russie sans donnes retournes.' };
  } catch (error) {
    // Gestion des erreurs avec plus de dtails
    console.error(`Erreur lors de la mise  jour de la salle de TPI (ID: ${roomId}): ${error.message}`);
    throw new Error(`Erreur de mise  jour: ${error.message}`);
  }
}

export const deleteTpiRoom = async roomId => {
  console.log(roomId)
  try {
    const response = await axios.delete(`${apiUrl}/delete-tpi-room/${roomId}`)
    return response.data
  } catch (error) {
    console.error(
      `Erreur lors de la suppression de la salle de TPI: ${error.message}`
    )
    throw error
  }
}