import axios from 'axios';

// Pour accder  la variable d'environnement REACT_APP_DEBUG
const debugMode = process.env.REACT_APP_DEBUG === 'true'; // Convertir en boolen si ncessaire
// Pour accder  la variable d'environnement REACT_APP_API_URL
const apiUrl = process.env.REACT_APP_API_URL;

const createUserUrl = `${apiUrl}/api/inscription`;
const getUsersUrl = `${apiUrl}/api/suivi-etudiants`;

export const createUser = async (userData) => {
  try {
    const response = await axios.post(createUserUrl, userData);
    return response.data;
  } catch (error) {
    console.error('Erreur lors de la cration de l\'utilisateur:', error);
    throw new Error('Une erreur est survenue lors de la cration de l\'utilisateur. Veuillez ressayer plus tard.');
  }
};

export const getUsers = async () => {
  try {
    const response = await axios.get(getUsersUrl);
    return response.data;
  } catch (error) {
    console.error('Erreur lors de la rcupration des utilisateurs:', error);
    throw new Error('Une erreur est survenue lors de la rcupration des utilisateurs. Veuillez ressayer plus tard.');
  }
};

export const updateUser = async (userId, userData) => {
  try {
    const response = await axios.put(`${apiUrl}/suivi-etudiants/${userId}`, userData);
    return response.data;
  } catch (error) {
    console.error('Erreur lors de la mise  jour de l\'utilisateur:', error);
    throw new Error('Une erreur est survenue lors de la mise  jour de l\'utilisateur. Veuillez ressayer plus tard.');
  }
};

export const deleteUser = async (userId) => {
  try {
    const response = await axios.delete(`${apiUrl}/suivi-etudiants/${userId}`);
    return response.data;
  } catch (error) {
    console.error('Erreur lors de la suppression de l\'utilisateur:', error);
    throw new Error('Une erreur est survenue lors de la suppression de l\'utilisateur. Veuillez ressayer plus tard.');
  }
};
